import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  OnInit
} from '@angular/core';
import { hotSafe, WindowRefService } from '@sofico-framework/utils';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { BehaviorSubject, fromEvent, merge, Observable } from 'rxjs';
import { filter, map, mapTo } from 'rxjs/operators';
import { NavItem } from './types/nav-item.type';

@UntilDestroy()
@Component({
  selector: 'sof-top-bar-nav',
  styleUrls: ['./top-bar-nav.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <nav class="navbar navbar-expand sof-navbar-light">
      <div class="container">
        <ul class="navbar-nav w-100">
          <li
            class="nav-item dropdown"
            *ngFor="
              let menuItem of menuItems;
              trackBy: trackByFn;
              let last = last
            "
            routerLinkActive="active"
            [class.active-menu-item]="
              menuItem?.children && (toggle$ | async) === menuItem
            "
          >
            <a
              class="d-flex justify-content-center sof-nav-link nav-link"
              [routerLink]="menuItem.routerLink"
              (click)="toggle$.next(null)"
              *ngIf="!menuItem?.children"
            >
              <ng-container
                *ngTemplateOutlet="innerText; context: { $implicit: menuItem }"
              ></ng-container>
            </a>
            <button
              class="d-flex justify-content-center sof-nav-link nav-link clickable"
              (click)="toggleMenuItem(menuItem)"
              *ngIf="menuItem?.children"
            >
              <ng-container
                *ngTemplateOutlet="innerText; context: { $implicit: menuItem }"
              ></ng-container>
            </button>
            <div
              class="dropdown-menu"
              [class.dropdown-menu-right]="last"
              [class.open-dropdown]="(toggle$ | async) === menuItem"
            >
              <ng-container
                *ngFor="
                  let subMenuItem of menuItem?.children;
                  trackBy: trackByFn
                "
              >
                <a
                  routerLinkActive="active"
                  class="dropdown-item"
                  [routerLink]="subMenuItem.routerLink"
                  (click)="toggle$.next(null)"
                >
                  <ng-container
                    *ngTemplateOutlet="
                      innerText;
                      context: { $implicit: subMenuItem }
                    "
                  ></ng-container>
                </a>
              </ng-container>
            </div>
          </li>
        </ul>
      </div>
    </nav>

    <ng-template #innerText let-item>
      <sof-svg-icon
        *ngIf="item.icon"
        [ngClass]="item.iconClasses"
        [icon]="item.icon"
      ></sof-svg-icon>
      <span>
        {{
          item?.label
            ? (tc + '.' + item.label | translate: item.params)
            : item.translation
        }}
      </span>
    </ng-template>
  `
})
export class TopBarNavComponent implements OnInit, OnDestroy, AfterViewInit {
  /**
   * The translation context
   */
  @Input() tc: string;
  /**
   * MenuItems - The menu only support one level down (children). NavItem with children itself will to trigger navigate.
   */
  @Input() menuItems: NavItem[];

  showMobile = false;

  // Presentation
  toggle$ = new BehaviorSubject<NavItem>(null);

  // Intermediate
  private clickedOutside$: Observable<undefined>;

  trackByFn = i => i;

  constructor(
    private elRef: ElementRef,
    private windowRefService: WindowRefService
  ) {}

  ngOnInit(): void {
    // Intermediate
    this.clickedOutside$ = this.getClickedOutside$();
  }

  ngAfterViewInit(): void {
    // windowRefService is only available after view init
    this.clickedOutside$
      .pipe(takeUntilDestroy(this))
      .subscribe(() => this.toggle$.next(null));
  }

  ngOnDestroy(): void {}

  toggleMobileMenu(): void {
    this.showMobile = !this.showMobile;
  }

  toggleMenuItem(menuItem: NavItem): void {
    if (this.toggle$.getValue() !== menuItem) {
      this.toggle$.next(menuItem);
    } else {
      this.toggle$.next(null);
    }
  }

  /**
   * Emits undefined if click on window was not this component
   */
  private getClickedOutside$(): Observable<undefined> {
    return merge(
      fromEvent(this.windowRefService.nativeWindow, 'click'),
      fromEvent(this.windowRefService.nativeWindow, 'touchstart')
    ).pipe(
      map((x: any) => {
        let currentElem = x.target;
        let found = false;
        while (!found && currentElem) {
          found = currentElem === this.elRef.nativeElement;
          currentElem = currentElem.parentElement;
        }
        return found;
      }),
      filter(x => !x),
      mapTo(undefined),
      hotSafe()
    );
  }
}
